import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

import Cv_en from "@/i18n/content/cv_en.json";
import Skills from "@/i18n/content/skills.json";
import Hobbies_en from "@/i18n/content/hobbies_en.json";
import Values_en from "@/i18n/content/values_en.json";

const filterCareerSteps = (careerSteps, filters) => {
    let filteredCareerSteps = [];
            careerSteps.forEach((careerElement) => {
                filters.forEach((filter) => {
                    if (careerElement.type === filter) {
                        filteredCareerSteps.push(careerElement);
                    }
                });
            });
     return filteredCareerSteps;
  }

  const filterSkillLanguage = (skills, language) => {
    skills.forEach((cat) => {
        cat.translatedCategory = deserialize(cat.category)[language]
        cat.content.forEach((content) => { 
            content.translatedDescription = deserialize(content.description)[language];     
            if(content.usage != undefined){
                content.translatedUsage =  deserialize(content.usage)[language];     
            }                 
        })
    })
  }

const deserialize = (json) =>  JSON.parse(JSON.stringify(json));



const importCvContent = (language) => import("@/i18n/content/cv_"+language+".json");

export default new Vuex.Store({
    state: {
        careerSteps: Cv_en,
        hobbies: Hobbies_en,
        values: Values_en,
        filteredCareerSteps: [],
        certificateIconUrl: "@/assets/certificate.svg",
        filters: [],
        skills: Skills
    },
    mutations: {
        FILTER_CAREER_STEPS(state, filters) {
            state.filters = filters;
   
            state.filteredCareerSteps = filterCareerSteps(state.careerSteps, filters);          
        },
        CHANGE_LANGUAGE(state, language) {
            importCvContent(language).then(function(data) {
                state.careerSteps = data.default;
                state.filteredCareerSteps = filterCareerSteps(state.careerSteps, state.filters); 
            });
            filterSkillLanguage(state.skills, language)
        }, 

    },
    actions: {
        setFilteredCareerSteps(state, filters) {
            state.commit("FILTER_CAREER_STEPS", filters);
        },
        setLanguage(state, language) {
            state.commit("CHANGE_LANGUAGE", language);                  
        },
    },
    modules: {},
    getters: {
        getFilteredCareerSteps: (state) => state.filteredCareerSteps,
        getCertificateIconUrl: (state) => state.certificateIconUrl,

        getSkills: (state) => state.skills,
        
        getHobbies: (state) => deserialize(state.hobbies),
        getValues: (state) => deserialize(state.values),
    },
});
