<template lang="">
    <div class="tasklist">
        <ul>
            <div class="tasktitle" v-for="(task, i) in tasks" :key="i" fill-dot>
                <DescriptionList v-bind:descriptions="task.description" v-bind:title="task.title" />
                <v-card-actions v-if="task.link">
                    <v-btn
                        text
                        :href="task.link"
                        color="secondary"
                        @click="reveal = true">
                    <v-icon>mdi-play-circle-outline</v-icon>
                        {{ $t("Try it out") }}
                    </v-btn>
                </v-card-actions>
            </div>
        </ul>
    </div>
</template>
<script>
import DescriptionList from "@/components/utils/DescriptionList";
export default {
    name: "CareerStepTask",

    components: {
        DescriptionList
    },

    props: {
        tasks: Array[Object],
    },
};
</script>

